import React, {useState} from 'react';
import classes from './BasketItem.module.css'

const BasketItem = ({callback}) => {
    const [count, setCount] = useState(1)
    const price=10000
    const [totalPrice, setTotalPrice] = useState(price*count)



    const increment = () => {
        setCount(count+1)
        setTotalPrice(price*(count+1))

    }
    const decrement = () => {
        if (count > 1){
            setCount(count -1)
            setTotalPrice(price*(count-1))
        }
    }

    return (
        <div className={classes.basketItem}>
            <div className={classes.basketItemPhoto}>
            </div>
            <div className={classes.basketItemContent}>
                <div className={classes.basketItemTitle}> </div>
                <div className={classes.basketItemSize}>: S</div>
                <div className={classes.basketItemPrice}>: 10 000</div>
                <div className={classes.basketCountLine}>
                    <div className={classes.basketItemCount}>
                        <div className={classes.basketCountIcon} onClick={decrement}>
                            -
                        </div>
                        {count}
                        <div className={classes.basketCountIcon} onClick={increment}>
                            +
                        </div>
                    </div>
                    <div className={classes.basketItemTotal}>{totalPrice}<div className={classes.rubleIcon}></div></div>
                </div>
            </div>
        </div>
    );
};

export default BasketItem;