import React, {useContext} from 'react';
import {Context} from "../../../index";
import classes from './ToolBar.module.css'
import ToolBtn from "../../UI/Buttons/ToolBtn/ToolBtn";
import {NavLink} from "react-router-dom";
import {
    BAGS_ROUTE,
    BOOTS_ROUTE,
    BRANDS_ROUTE,
    CATALOGUE_ROUTE,
    CLOTHES_ROUTE,
    JEWELLERY_ROUTE,
    SHOP_ROUTE
} from "../../../utils/consts";

const ToolBar = () => {
    const {user} = useContext(Context)
    const tools = [
        {id: 1, title: '', path: SHOP_ROUTE},
        {id: 2, title: '', path: CATALOGUE_ROUTE},
        {id: 3, disabled: true, title: '', path: BRANDS_ROUTE},
        {id: 4, title: '', path: CLOTHES_ROUTE},
        {id: 5, title: '', path: BOOTS_ROUTE},
        {id: 6, title: '', path: BAGS_ROUTE},
        {id: 7, title: '', path: JEWELLERY_ROUTE},
    ]

    return (
        <div className={classes.toolBar}>
            {tools.map((tool)=>
                <NavLink key={tool.path} to={tool.path}><ToolBtn disabled={tool.disabled} key={tool.id} title={tool.title}/></NavLink>
            )}
        </div>
    );
};

export default ToolBar;