import React, {useContext} from 'react';
import classes from './AuthForm.module.css';
import AuthRow from "../AuthRow/AuthRow";
import MyInput from "../Inputs/MyInput/MyInput";
import {NavLink, useLocation} from "react-router-dom";
import {LOGIN_ROUTE, REGISTRATION_ROUTE, SHOP_ROUTE} from "../../../utils/consts";
import MyButton from "../Buttons/MyButton/MyButton";
import {Context} from "../../../index";
import Logo from "../../Logo/Logo";

const AuthForm = () => {
    const location = useLocation()
    const isLogin = location.pathname === LOGIN_ROUTE
    console.log(location)
    const {user} = useContext(Context)

    return (
        <div className={classes.authPage}>
            <Logo/>
            <form className={classes.authForm}>
                <div className={classes.title}>{isLogin ? '' : ''}</div>
                <AuthRow
                    leftIcon={classes.authIcon + ' ' + classes.emailIcon}
                    rowClass={classes.row}
                    rightIcon={classes.authIcon}
                    element={<MyInput type={'email'} placeholder={''}/>}
                />
                <AuthRow
                    leftIcon={classes.authIcon + ' ' + classes.passwordIcon}
                    rowClass={classes.row}
                    rightIcon={classes.authIcon + ' ' + classes.eyeIcon}
                    element={<MyInput type={'password'} placeholder={''}/>}
                />
                {isLogin
                    ? null
                    : <AuthRow
                        leftIcon={classes.authIcon + ' ' + classes.passwordIcon}
                        rowClass={classes.row}
                        rightIcon={classes.authIcon + ' ' + classes.eyeIcon}
                        element={<MyInput type={'password'} placeholder={' '}/>}
                    />
                }
                {isLogin
                    ? <AuthRow
                        leftIcon={classes.authIcon}
                        rowClass={classes.row}
                        rightIcon={classes.authIcon}
                        element={<NavLink to={SHOP_ROUTE} style={{width: '100%'}}>
                            <MyButton
                                onClick={() => {
                                    user.setIsAuth(true)
                                }}
                                title={''}
                                style={{
                                    fontSize: '20px'
                                }}
                            />
                        </NavLink>}
                    />
                    : <AuthRow
                        leftIcon={classes.authIcon}
                        rowClass={classes.row}
                        rightIcon={classes.authIcon}
                        element={<NavLink to={SHOP_ROUTE} style={{width: '100%'}}>
                            <MyButton
                                onClick={() => {
                                    user.setIsAuth(true)
                                }}
                                title={' '}
                                style={{
                                    fontSize: '20px'
                                }}/>
                        </NavLink>}
                    />
                }
                <AuthRow
                    leftIcon={classes.authIcon}
                    rowClass={classes.row}
                    rightIcon={classes.authIcon}
                    element={<div className={classes.authText}>  ?</div>}
                />
                {isLogin
                    ? <AuthRow
                        leftIcon={classes.authIcon}
                        rowClass={classes.row}
                        rightIcon={classes.authIcon}
                        element={<NavLink className={classes.authText + ' ' + classes.toRegistration}
                                          to={REGISTRATION_ROUTE}>
                            <div>   </div>
                        </NavLink>}
                    />
                    : <AuthRow
                        leftIcon={classes.authIcon}
                        rowClass={classes.row}
                        rightIcon={classes.authIcon}
                        element={<NavLink className={classes.authText + ' ' + classes.toRegistration}
                                          to={LOGIN_ROUTE}>
                            <div>  ?</div>
                        </NavLink>}
                    />
                }

            </form>
        </div>
    );
};

export default AuthForm;