import React, {useContext, useMemo, useState} from 'react';
import classes from './FilterRaw.module.css'
import {observer} from "mobx-react-lite";
import {Context} from "../../../index";

const FilterRaw = observer(({name, onClick, selected, image}) => {
    return (
        <div onClick={(e) => {
            onClick()
        }} className={!selected ? classes.filterRaw : classes.filterRaw + ' ' + classes.clicked}>
            <div className={classes.filterRawIcon + ' ' + image}></div>
            <div className={classes.filterRawText}>{name}</div>
        </div>
    );
});

export default FilterRaw;